@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.title{
    background-color: rgb(122, 142, 255);
    font-size: 40px;
    color: white;
    width: 1040px;
    padding-top: 20px;
    padding-bottom: 20px;
    margin: auto;
    border-radius: 10px 10px 0 0;
    font-weight: 700;
}

.options{
    display: flex;
    justify-content: space-around;
    width: 1040px;
    padding-top: 20px;
    padding-bottom: 20px;
    background-color: white;
    margin: auto;
    
}

.selectAlg {
    width: 500px;
}
.selectSpeed {
    width: 500px;
}

.selectTitle{
    font-size: 25px;
    font-weight: 500;
    color: black;
    padding: 10px;
}

.selectChoices{
    display: flex;
    justify-content: center;
    font-size: 12px;
    padding-left: 10px;
    padding-right: 10px;
}
.choice{
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    height: 30px;
    color: black;
    cursor: pointer;
    
    width: 20%;
    display: grid;
    place-items: center;
}
.choiceSelected{
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    height: 30px;
    color: white;  
    background-color: black;
    cursor: pointer;
    
    width: 20%;
    display: grid;
    place-items: center;
    
}
.choice:hover{
    text-decoration: underline; 
    
}

@media only screen and (max-width: 1040px) {
    .title{
        width: 100%;
        border-radius: 0;
        
        
    }
    .options{
        width: 100%;
    }
   

  }

  @media only screen and (max-width: 900px) {
    .selectChoices{
        font-size: 10px;
    }
  }

  @media only screen and (max-width: 400px) {
    
    .selectTitle{
        font-size: 20px;
    }
    .selectChoices{
        font-size: 7px;
        letter-spacing: 0px;
        padding-left: 2px;
        padding-right: 2px;
    }
  }
  
.visualizerContainer{
  width: 1040px;
  margin: auto;
  
  
}
.visualizer{
    background-color: rgb(122, 142, 255);
    height: 300px;
    display: flex;
    justify-content: space-evenly;
    transform: scaleY(-1);
    padding-left: 20px;
    padding-right: 20px;
}
.line {
    background-color: #ffd900;
    width: 1px;
    display: inline-block;
    margin-bottom: 0;
    
  }

  .btnContainer{
    display: flex;
    justify-content: space-around;
    width: 1040px;
    padding-top: 20px;
    padding-bottom: 20px;
    margin: auto;
    background-color: white;
    
  }

  .btn{
    width: 130px;
    padding:10px;
    color: black;
    border-radius: 2px;
    border: 1px solid black;
  }
  .btn:hover{
    cursor: pointer;
    background-color: black;
    color: white;
    transition-duration: 0.1s;
  }

  .description{
    background-color: rgb(122, 142, 255);
    font-weight: 700;
    display: flex;
    justify-content: space-around;
    width: 1040px;
    height: 120px;
    margin: auto;
    border-radius: 0 0 10px 10px ;
    color: rgb(255, 255, 255);
  }
  .descriptionText{
    padding: 22px;
    margin: auto;
  }
  
  @media only screen and (max-width: 1040px) {
    .visualizerContainer{
      width: 100%;
    }
    .btnContainer{
      width: 100%;
    }
    .description{
      width: 100%;
      height: 100%;
      border-radius: 0;
      
    }
  }

  @media only screen and (max-width: 400px) {
   .description{
     border-radius: 0;
   }
  }
.App {
  text-align: center;
  font-family: 'Barlow Semi Condensed', sans-serif;
  
  
  
}

.main{
  margin: auto;
  margin-top: 20px;
  margin-bottom: 20px;
  border-radius: 10px;
  box-shadow: 1px 5px 8px #888888;
  width: 1040px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


::-webkit-scrollbar {
  width: 2cpx;
}

::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
::-webkit-scrollbar-thumb {
  background: #888; 
}

::-webkit-scrollbar-thumb:hover {
  background: #555; 
}


@media only screen and (max-width: 1040px) {
  .main{
    width: 100%;
  }
}
@media only screen and (max-width: 400px) {
  .main{
    margin-top: 0;
    margin-bottom: 0;
    box-shadow: none;
  }
}
